.loading-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 65vh;
  background-color: lightgoldenrodyellow;
  font-family: Arial, sans-serif;
  text-align: center;
}

.loading-screen h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.duck-container {
  display: flex;
  gap: 20px;
}

.duck {
  width: 150px;
  height: auto;
}

.you-btw {
  font-size: 1.5rem;
  font-weight: bold;
  margin-top: 10px;
}
